<template>
    <div>
      <h2 style="width:80%;text-align: center">小区实时流量需求</h2>
      <el-table border :data="tableData" :span-method="arraySpanMethod" style="width: 80%;margin-top: 15px;">
        <el-table-column type="index" label="序号" width="60px" align="center" ></el-table-column>
        <el-table-column prop="plotName" label="小区名称" min-width="30%" align="center"></el-table-column>
        <el-table-column prop="needWater" label="流量需求数" min-width="30%" align="center"></el-table-column>
      </el-table>
    </div>
</template>

<script>
export default {
  name: 'realTimeWater',
  data () {
    return {
      tableData: [{
        plotName: '荆州新天地',
        needWater: '53'
      },
      {
        plotName: '荆门新天地',
        needWater: '110'
      },
      {
        plotName: '荆州碧桂园',
        needWater: '153'
      }],
      time: '2019-02-02 13:20:20',
      lastRow: {
        plotName: '更新周期（min）：10',
        needWater: ''
      }
    }
  },
  beforeMount () {
    this.lastRow.needWater = '系统时间：' + this.time
    this.tableData.push(this.lastRow)
  },
  methods: {
    arraySpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === this.tableData.length - 1) {
        if (columnIndex === 0) {
          return [0, 0]
        } else if (columnIndex === 1) {
          return [1, 2]
        }
      }
    }
  }
}
</script>

<style scoped>
</style>



